:root {
  --color-main: #f1eee9;
  --color-primary: #ad7f95;
  --color-secondary: #6a503e;
  --color-dark: #1e1f1e;
  --color-light: #fff;
  --text-button: #ffffff;
  --color-button-trans: #fff;
  --vd-color-pink: #e9088a;
  --border-radius: 500px;
}

body {
  font-family: "Nunito Sans", sans-serif;
  background-color: var(--color-main);
  color: var(--color-dark);
  hyphens: manual;
  text-rendering: optimizeLegibility;
}

.main-app {
  position: relative;
  overflow: hidden;
}
.main-app .background-body-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-app .background-body-wrapper .background-body-content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 767px) {
  .main-app .background-body-wrapper .background-body-content img {
    object-position: right;
    object-fit: contain;
  }
}
.main-app .elements-wrapper-absolute {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-app .elements-wrapper-absolute .background-element {
  height: 100%;
}
.main-app .elements-wrapper-absolute .background-element img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.main-app .elements-absolute {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto;
}
.main-app .elements-absolute .background-element {
  position: relative;
  width: 100%;
  height: auto;
}
.main-app .elements-absolute .background-element img {
  width: 100%;
  height: auto;
}
.main-app .ornaments-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.main-app .bg-element {
  position: relative;
  width: 100%;
  height: 100%;
}
.main-app .bg-fluid {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo-viding {
  height: 2.4rem;
}
@media (max-width: 767px) {
  .logo-viding {
    height: 2.25rem;
  }
}
.logo-viding path {
  fill: var(--vd-color-pink);
}

h1,
h2,
h3 {
  font-family: "Prata", serif;
}

h1 {
  font-size: 38px;
  line-height: 0.8;
}
@media (max-width: 767px) {
  h1 {
    font-size: 36px;
  }
}
h1 span {
  font-size: 26px;
  display: block;
  transform: translateY(1.5rem);
}
@media (max-width: 767px) {
  h1 span {
    font-size: 20px;
  }
}

small {
  font-size: 12px;
}

span {
  font-size: 16px;
}

@media (max-width: 575px) {
  p,
  span {
    font-size: 14px;
  }
  small {
    font-size: 10px;
  }
}
.image-element {
  position: relative;
  width: 100%;
  height: auto;
}

.img-fluid {
  width: 100%;
  height: auto;
}

@media (max-width: 767px) {
  .container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

.btn-custom {
  background-color: var(--color-secondary);
  padding: 0.5rem 2rem;
  transition: all 0.3s ease-in-out;
  color: var(--text-button);
  width: fit-content;
  margin: 0 auto;
  border-radius: 50px;
}
.btn-custom:hover {
  color: var(--text-button);
}
.btn-custom path {
  fill: var(--text-button);
}

.overlay-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 1000px 1000px 50px 50px;
  background-color: var(--color-secondary);
}

.font-primary {
  font-family: "Nunito Sans", sans-serif;
}

.font-secondary {
  font-family: "Prata", serif;
}

.scaleX-reverse {
  transform: scaleX(-1);
}

.scaleY-reverse {
  transform: scaleY(-1);
}

.scale-reverse {
  transform: scale(-1);
}

.form-control,
.form-select {
  font-family: "Nunito Sans", sans-serif;
  border: none;
  padding: 0.5rem 1rem;
  background-color: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}
.form-control::placeholder,
.form-select::placeholder {
  color: var(--color-dark);
}
.form-control:focus,
.form-select:focus {
  box-shadow: none;
  border-color: var(--color-dark);
}

.scrolled {
  background: #fff !important;
  transition: all 1s ease-in-out;
  box-shadow: 3px 0px 15px 15px rgba(89, 89, 89, 0.2);
  visibility: visible !important;
  pointer-events: all !important;
}
.scrolled .logo-viding path {
  fill: var(--vd-color-pink);
}

.navbar-hide {
  top: -70px;
}

.progress {
  position: fixed;
  top: 0;
  left: 0;
  height: 2px;
  background-color: var(--vd-color-pink);
  width: 100%;
  transform: scaleZ(0);
  transform-origin: 0% 50%;
  z-index: 9999;
}

.title-section {
  position: relative;
  width: fit-content;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 1.25rem;
}
.title-section h2,
.title-section p,
.title-section span {
  margin-bottom: 0;
  font-weight: 500;
  font-size: 32px;
  padding: 0 1rem;
  overflow: hidden;
}
@media (max-width: 767px) {
  .title-section h2,
  .title-section p,
  .title-section span {
    font-size: 2rem;
  }
}

.overlay-elements-gradient {
  position: absolute;
  left: 0;
  width: 100%;
  height: 30%;
}
.overlay-elements-gradient.overlay-bottom {
  bottom: -5px;
  background: linear-gradient(to top, var(--color-main) 20%, transparent 100%);
}
.overlay-elements-gradient.overlay-top {
  top: -5px;
  background: linear-gradient(to bottom, var(--color-main) 20%, transparent 100%);
}

.waves-bottom {
  position: absolute;
  width: 100%;
  left: 0;
  height: auto;
  bottom: -2px;
  z-index: 3;
}
.waves-bottom path {
  fill: var(--color-main);
}
.waves-bottom.reverse {
  transform: scaleX(-1);
}

.waves-top {
  position: absolute;
  top: -2px;
  left: 0;
  width: 100%;
  height: auto;
  transform: scale(-1);
  z-index: 3;
}
.waves-top path {
  fill: var(--color-main);
}

.waves-left {
  position: absolute;
  height: 100%;
  left: -1px;
  top: 0;
}

.waves-right {
  position: absolute;
  height: 100%;
  right: -1px;
  top: 0;
  transform: scale(-1);
}

ul {
  padding: 0;
}
ul li {
  padding: 0;
}

@media (min-width: 768px) {
  .lg {
    display: block;
  }
  .sm {
    display: none;
  }
}
@media (max-width: 767px) {
  .lg {
    display: none;
  }
  .sm {
    display: block;
  }
}
.loader-wrapper {
  position: fixed;
  min-height: 100vh;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 1500;
  flex-direction: column;
  gap: 1.25rem;
}
.loader-wrapper .spinner {
  position: relative;
  width: 84px;
}
.loader-wrapper span {
  color: var(--vd-color-pink);
  font-weight: 700;
  animation: fade 2s linear infinite;
  letter-spacing: 5px;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.modal.show-maps .modal-content {
  border-radius: 20px;
}
.modal.show-maps .modal-body {
  padding: 2rem 3rem;
  position: relative;
}
@media (max-width: 767px) {
  .modal.show-maps .modal-body {
    padding: 2rem 1rem;
  }
}
.modal.show-maps .modal-body .btn-modal {
  background-color: var(--color-light);
  color: var(--color-dark);
  position: absolute;
  right: 10px;
  top: 10px;
  border-radius: 50px;
  opacity: 0.5;
  padding: 0 0.425rem;
  transition: all 0.5s ease;
}
.modal.show-maps .modal-body .btn-modal:hover {
  opacity: 1;
}
.modal.show-maps .modal-body .btn-custom {
  background: transparent;
  border-color: var(--color-dark);
  color: var(--color-dark);
  transition: all 0.25 ease-in-out;
}
.modal.show-maps .modal-body .btn-custom:hover {
  background: var(--color-secondary);
  border-color: var(--color-secondary);
  color: var(--text-button);
}
.modal.show-maps .modal-body .maps-element {
  position: relative;
  width: 100%;
  aspect-ratio: 16/9;
}
@media (max-width: 767px) {
  .modal.show-maps .modal-body .maps-element {
    aspect-ratio: 9/10;
  }
}
.modal.show-maps .modal-body .loader-wrapper-modal {
  display: block;
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal.show-maps .modal-body .loader-wrapper-modal.loaded {
  display: none;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: var(--color-secondary);
  border-radius: 50%;
  animation: lds-default 1.2s linear infinite;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.modal.show-maps .modal-body .loader-wrapper-modal .lds-default div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes lds-default {
  0%, 20%, 80%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
.modal.show-maps .modal-body .modal-actions {
  position: relative;
  color: var(--color-dark);
}

[data-anim][data-anim] {
  will-change: transform;
}
[data-anim][data-anim].animation-invisible {
  visibility: hidden;
}
[data-anim][data-anim].has-animate {
  animation-duration: 3500ms;
  animation-timing-function: ease-in-out;
}
[data-anim][data-anim].animate-loop {
  animation-duration: 4000ms;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
[data-anim][data-anim].animate-loop.animate-paused {
  animation-play-state: paused;
}

[data-anim=fade].has-animate {
  animation-name: fadeAnim;
}

[data-anim=fade-right].has-animate {
  animation-name: fadeRight;
}

[data-anim=fade-left].has-animate {
  animation-name: fadeLeft;
}

[data-anim=fade-up].has-animate {
  animation-name: fadeUp;
}

[data-anim=fade-down].has-animate {
  animation-name: fadeDown;
}

@keyframes fadeAnim {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeRight {
  0% {
    transform: translate3d(-100px, 0, 0);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fadeLeft {
  0% {
    transform: translate3d(100px, 0, 0);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fadeUp {
  0% {
    transform: translate3d(0, 100px, 0);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
@keyframes fadeDown {
  0% {
    transform: translate3d(0, -100px, 0);
    opacity: 0;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}
[data-anim=slide-right].has-animate {
  animation-name: slideRight;
}

[data-anim=slide-right-2].has-animate {
  animation-name: slideRight2;
}

[data-anim=slide-left].has-animate {
  animation-name: slideLeft;
}

[data-anim=slide-left-2].has-animate {
  animation-name: slideLeft2;
}

[data-anim=slide-img-right].has-animate {
  animation-name: slideImgRight;
}

[data-anim=slide-img-left].has-animate {
  animation-name: slideImgLeft;
}

[data-anim=slide-up].has-animate {
  animation-name: slideUp;
}

[data-anim=slide-down].has-animate {
  animation-name: slideDown;
}

@keyframes slideRight {
  0% {
    transform: translate3d(-50px, 0, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
@keyframes slideRight2 {
  100% {
    transform: translate3d(50px, 0, 0);
  }
}
@keyframes slideLeft {
  0% {
    transform: translate3d(10%, 0, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
@keyframes slideLeft2 {
  100% {
    transform: translate3d(-50px, 0, 0);
  }
}
@keyframes slideImgRight {
  0% {
    object-position: 100% 50%;
  }
}
@keyframes slideImgLeft {
  0% {
    object-position: 0% 50%;
  }
}
@keyframes slideUp {
  0% {
    transform: translate3d(0, 50%, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
@keyframes slideDown {
  0% {
    transform: translate3d(0, -100px, 0);
  }
  100% {
    transform: translateZ(0);
  }
}
[data-anim=slide-rotate-left].has-animate {
  animation-name: slideRotateLeft;
}

[data-anim=slide-rotate-right].has-animate {
  animation-name: slideRotateRight;
}

[data-anim=slide-rotate-up].has-animate {
  animation-name: slideUpRotateLeft;
}

@keyframes slideRotateLeft {
  0% {
    transform: translate3d(30%, 0, 0) rotate(45deg);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotate(0);
  }
}
@keyframes slideRotateRight {
  0% {
    transform: translate3d(-30%, 0, 0) rotate(-45deg);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotate(0);
  }
}
@keyframes slideUpRotateLeft {
  0% {
    transform: translate3d(0, 150px, 0) rotate(30deg);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) rotateZ(0);
  }
}
[data-anim=rotate-right].has-animate {
  animation-name: rotateRight;
}

[data-anim=rotate-left].has-animate {
  animation-name: rotateLeft;
}

@keyframes rotateRight {
  0% {
    transform: rotateZ(0);
  }
  100% {
    transform: rotate(10deg);
  }
}
@keyframes rotateLeft {
  0% {
    transform: rotateZ(0);
  }
  100% {
    transform: rotate(-10deg);
  }
}
[data-anim=zoom-in].has-animate {
  animation-name: zoomIn;
}

[data-anim=zoom-in-up].has-animate {
  animation-name: zoomInUp;
}

[data-anim=zoom-in-down].has-animate {
  animation-name: zoomInDown;
}

[data-anim=zoom-in-left].has-animate {
  animation-name: zoomInLeft;
}

[data-anim=zoom-in-right].has-animate {
  animation-name: zoomInRight;
}

[data-anim=zoom-out].has-animate {
  animation-name: zoomOut;
}

[data-anim=zoom-out-up].has-animate {
  animation-name: zoomOutUp;
}

[data-anim=zoom-out-down].has-animate {
  animation-name: zoomOutDown;
}

@keyframes zoomIn {
  0% {
    transform: scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes zoomInUp {
  0% {
    transform: translate3d(0, 100px, 0) scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomInDown {
  0% {
    transform: translate3d(0, -100px, 0) scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomInLeft {
  0% {
    transform: translate3d(50%, 0, 0) scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomInRight {
  0% {
    transform: translate3d(-50%, 0, 0) scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomOut {
  0% {
    transform: scale3d(1.3, 1.3, 1.3);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes zoomOutUp {
  0% {
    transform: translate3d(0, 100px, 0) scale3d(1.3, 1.3, 1.3);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
@keyframes zoomOutDown {
  0% {
    transform: translate3d(0, -100px, 0) scale3d(1.3, 1.3, 1.3);
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    transform: translateZ(0) scale3d(1, 1, 1);
  }
}
[data-anim*=flip][data-anim*=flip] {
  backface-visibility: hidden;
}

[data-anim=flip-up].has-animate {
  animation-name: flipUp;
}

[data-anim=flip-down].has-animate {
  animation-name: flipDown;
}

[data-anim=flip-left].has-animate {
  animation-name: flipLeft;
}

[data-anim=flip-right].has-animate {
  animation-name: flipRight;
}

@keyframes flipUp {
  0% {
    perspective: 2500px;
    transform: rotateX(100deg);
  }
  100% {
    perspective: 0;
    transform: rotateX(0);
  }
}
@keyframes flipDown {
  0% {
    perspective: 2500px;
    transform: rotateX(-100deg);
  }
  100% {
    perspective: 0;
    transform: rotateX(0);
  }
}
@keyframes flipLeft {
  0% {
    perspective: 2500px;
    transform: rotateY(-100deg);
  }
  100% {
    perspective: 0;
    transform: rotateY(0);
  }
}
@keyframes flipRight {
  0% {
    perspective: 2500px;
    transform: rotateY(100deg);
  }
  100% {
    perspective: 0;
    transform: rotateY(0);
  }
}
[data-anim=height-up].has-animate {
  animation-name: heightUp;
  visibility: visible !important;
}

[data-anim=height-up-reverse].has-animate {
  animation-name: heightUpReverse;
  animation-fill-mode: forwards;
  visibility: visible !important;
}

[data-anim=height-down].has-animate {
  animation-name: heightDown;
  visibility: visible !important;
}

[data-anim=height-down-reverse].has-animate {
  animation-name: heightDownReverse;
  animation-fill-mode: forwards;
  visibility: visible !important;
}

[data-anim=height-center].has-animate {
  animation-name: heightCenter;
  visibility: visible !important;
}

[data-anim=height-center-reverse].has-animate {
  animation-name: heightCenterReverse;
  animation-fill-mode: forwards;
  visibility: visible !important;
}

[data-anim=width-left].has-animate {
  animation-name: widthLeft;
  visibility: visible !important;
}

[data-anim=width-left-reverse].has-animate {
  animation-name: widthLeftReverse;
  animation-fill-mode: forwards;
  visibility: visible !important;
}

[data-anim=width-right].has-animate {
  animation-name: widthRight;
  visibility: visible !important;
}

[data-anim=width-right-reverse].has-animate {
  animation-name: widthRightReverse;
  animation-fill-mode: forwards;
  visibility: visible !important;
}

[data-anim=width-center].has-animate {
  animation-name: widthCenter;
  visibility: visible !important;
}

[data-anim=width-center-reverse].has-animate {
  animation-name: widthCenterReverse;
  animation-fill-mode: forwards;
  visibility: visible !important;
}

@keyframes heightUp {
  0% {
    clip-path: inset(100% 0 0 0);
  }
  100% {
    clip-path: inset(0% 0 0 0);
  }
}
@keyframes heightDown {
  0% {
    clip-path: inset(0 0 100% 0);
  }
  100% {
    clip-path: inset(0 0 0% 0);
  }
}
@keyframes heightUpReverse {
  0% {
    transform: translateZ(0) scaleY(1);
  }
  100% {
    transform: translate3d(0, 50%, 0) scaleY(0);
  }
}
@keyframes heightDownReverse {
  0% {
    transform: translateZ(0) scaleY(1);
  }
  100% {
    transform: translate3d(0, -50%, 0) scaleY(0);
  }
}
@keyframes heightCenter {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes heightCenterReverse {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}
@keyframes widthLeft {
  0% {
    transform: translate3d(50%, 0, 0) scaleX(0);
  }
  100% {
    transform: translateZ(0) scaleX(1);
  }
}
@keyframes widthRight {
  0% {
    transform: translate3d(-50%, 0, 0) scaleX(0);
  }
  100% {
    transform: translateZ(0) scaleX(1);
  }
}
@keyframes widthCenter {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}
@keyframes widthLeftReverse {
  0% {
    transform: translateZ(0) scaleX(1);
  }
  100% {
    transform: translate3d(50%, 0, 0) scaleX(0);
  }
}
@keyframes widthRightReverse {
  0% {
    transform: translateZ(0) scaleX(1);
  }
  100% {
    transform: translate3d(-50%, 0, 0) scaleX(0);
  }
}
@keyframes widthCenterReverse {
  0% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}
